.logo-container {
	display: flex; 
	align-items: flex-start;
	justify-content: center;

	height: 100vh;
	width: 100vw;
	margin: 0;
	padding: 0;

	margin-bottom: 20px;
}

.logo-container-inner {
	position: sticky;
	display: flex; 
	align-items: center;
	justify-content: center;
	width: 300px;
	height: 300px;
	
	top: calc(50vh - 150px);
	
	animation: logo-in 1.3s ease-in-out normal 1;
	z-index: -1;


}

@keyframes logo-in {
	/* Brain */
	0% {
		margin-bottom: -300px;
		top: 100%;
	}
	100% {
		margin-bottom: 0px;
		top: calc(50vh - 150px);
	}
}

.title-text {
	margin-left: -300px;
	margin-top: 20px;
	

	display: flex; 
	align-items: center;
	justify-content: center;
	
	z-index: 10;
	font-size: 30px;
	font-weight: 900;
	color: #fff;
	text-align: center;
}

.animated-logo {
	height: 300px;
	width: 300px;

	display:flex;
	justify-content: flex-start;
	align-items: flex-start;

	
}

.animated-logo::before {
	position: relative;
	display:flex;
	justify-content: flex-start;
	align-items: flex-start;

	background-color:#000;
	color: #fff;
	margin:0px;

	width: 300px;
	height: 300px;
	margin-left: -82px;
	content:"";



	clip-path: path('M 135 3 C 135 3 141 0 144 3 Q 141 21 156 45 C 180 45 180 30 210 30 C 270 30 285 105 285 135 C 285 180 267 210 246 255 C 237 270 219 300 195 300 C 165 300 165 285 150 285 C 135 285 135 300 105 300 C 81 300 63 270 54 255 C 33 210 15 180 15 135 C 15 105 30 30 90 30 C 120 30 120 45 144 45 Q 129 21 129 6 C 129 6 129 6 135 3');
	
  	animation: logo-animation 7s ease-in-out normal infinite;
}


@keyframes logo-animation {
	/* Brain */
	0% {
		clip-path: var(--brain-path);
	}
	28.3% {
		clip-path: var(--brain-path);

		background-color: #000;
	}

	/* Apple*/
	33.3% {
		clip-path: var(--apple-path);

		/*background-color: red;*/
	}
	61.7% {
		clip-path: var(--apple-path);

		/*background-color: red;*/
	}

	/* Bat */
	66.7% {
		clip-path: var(--bat-path);

		background-color: #000000;
	}
	95% {
		clip-path: var(--bat-path);

	
		background-color: #000000;
	}

	/* Brain */
	100% {
		clip-path: var(--brain-path);

	}
}

